// src/utils/exportUtils.js
import * as XLSX from 'xlsx';
import { formatCurrency, formatDateForExport } from './formatUtils';

export const exportToExcel = (data) => {
  if (!Array.isArray(data) || data.length === 0) {
    alert('Khng c d liu  xut');
    return;
  }

  try {
    // Chun b d liu  xut
    const exportData = data.map(item => {
      try {
        return {
          'Ngy gii ngn': formatDateForExport(item["Ngy gii ngn"]),
          'Thi gian': formatDateForExport(item["Thi gian"]),
          'Khu vc': item["KHU VC"] || '',
          'D n': item["D n"] || '',
          'M CHI': item["M CHI"] || '',
          'S tin': item["S TIN"] ? parseFloat(item["S TIN"]) : 0,
          'Nhn vin': item["NHN VIN THC HIN"] || '',
          'Ni dung': item["NI DUNG"] || '',
          'Trng thi': item["TRNG THI"] || ''
        };
      } catch (error) {
        console.error('Li khi x l dng d liu:', error, item);
        return null;
      }
    }).filter(Boolean);

    // Tnh tng
    const totalAmount = data.reduce((sum, item) => {
      const amount = item["S TIN"] ? parseFloat(item["S TIN"]) : 0;
      return sum + amount;
    }, 0);

    // Thm dng tng
    exportData.push({
      'Ngy gii ngn': 'TNG CNG',
      'Thi gian': '',
      'Khu vc': '',
      'D n': '',
      'M CHI': '',
      'S tin': totalAmount,
      'Nhn vin': '',
      'Ni dung': '',
      'Trng thi': ''
    });

    // To worksheet
    const ws = XLSX.utils.json_to_sheet(exportData);

    // nh dng ct
    ws['!cols'] = [
      { wch: 20 }, // Ngy gii ngn
      { wch: 20 }, // Thi gian
      { wch: 15 }, // Khu vc
      { wch: 20 }, // D n
      { wch: 15 }, // M CHI
      { wch: 20 }, // S tin
      { wch: 20 }, // Nhn vin
      { wch: 50 }, // Ni dung
      { wch: 15 }  // Trng thi
    ];

    // Style cho dng tng
    const lastRow = exportData.length;
    ['A', 'F'].forEach(col => {
      if (!ws[`${col}${lastRow}`]) {
        ws[`${col}${lastRow}`] = { v: '', s: {} };
      }
      ws[`${col}${lastRow}`].s = {
        font: { bold: true },
        fill: { fgColor: { rgb: "FFEB9C" } }
      };
    });

    // To workbook v lu file
    const wb = XLSX.utils.book_new();
    XLSX.utils.book_append_sheet(wb, ws, "Chi ph");
    
    const fileName = `Bao_cao_chi_phi_${new Date().toLocaleDateString('vi-VN').replace(/\//g, '_')}.xlsx`;
    XLSX.writeFile(wb, fileName);

  } catch (error) {
    console.error('Li khi xut Excel:', error);
    alert('C li xy ra khi xut file Excel. Vui lng th li.');
  }
};